.w{
	width: 1180px;
	margin: 0 auto;
	background-color: rgba(6,6,6,0.1);
}

*{
	padding: 0;
	margin: 0;
	text-decoration: none;
}

ul li{
	list-style: none;
}

img{
	display: block;
	border:none;
}

.l{
	width: 885px;
	margin: 0 auto;
	background-color: orange;
}
.clearfix:after,
.clearfix:before{
	content: '.';
	height: 0;
	line-height: 0;
	visibility: hidden;
	display: none;
	clear: both;
}

.clearfix{
	zoom: 1;
}

body{
	background-color: #FFEFD5;
}


