
.foodwrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-right: 1px;
  background-color: white;
}
.foodwrapper .foodcard {
  position: relative;
  width: 295px;
  height: 138px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.foodwrapper .foodcard .foodleft {
  height: 98px;
  padding: 20px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.foodwrapper .foodcard .foodleft .foodimg {
  width: 70px;
  height: 70px;
}
.foodwrapper .foodcard .foodleft .foodimg img {
  display: block;
  position: relative;
  height: 70px;
  width: 70px;
}
.foodwrapper .foodcard .foodleft .deliverytime {
  font-size: 12px;
  line-height: 28px;
}
.foodwrapper .foodcard .foodright {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 20px 0;
  text-align: left;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.foodwrapper .foodcard .foodright .foodname {
  font-size: 16px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 165px;
}
.foodwrapper .foodcard .foodright .foodranting span {
  font-size: 12px;
}
.foodwrapper .foodcard .foodright .fooddeverycost {
  font-size: 12px;
}
.foodwrapper .foodcard .foodright .foodsupport {
  font-size: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.foodwrapper .foodcard .foodright .foodsupport span {
  font-size: 12px;
  border: 1px solid #14ae18;
  color: red;
  border-radius: 3px;
  padding: 3px 6px;
}
.fooddetail {
  position: absolute;
  left: 295px;
  display: none;
  z-index: 100;
}
.sequence {
  cursor: pointer;
  text-align: left;
}
.sequence span {
  display: inline-block;
  width: 200px;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
  font-weight: 300;
  text-align: center;
}
.sequence span:hover {
  background-color: pink;
}



