
.mini-card {
  top: 0;
  left: 310px;
  width: 290px;
  padding: 10px;
  background-color: #FFEBCD;
  color: black;
}
.mini-card .mini-name {
  font-size: 16px;
  line-height: 34px;
  height: 34px;
  font-weight: 700;
  border-bottom: 1px solid gray;
}
.mini-card .mini-supports {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.mini-card .mini-supports .mini-support {
  margin: 10px 0;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mini-card .mini-supports .mini-support span {
  display: inline-block;
  font-size: 12px;
  text-align: left;
  margin-right: 10px;
}
.mini-card .mini-supports .mini-support span:nth-child(1) {
  border: 1px solid gray;
  width: 16px;
  height: 16px;
  text-align: center;
}
.mini-card .mini-delivery {
  font-size: 14px;
  padding: 5px;
  margin: 10px;
  text-align: left;
  background-color: #BBFFFF;
}
.mini-card .mini-delivery span {
  padding: 0 10px;
}
.mini-card .mini-delivery span:nth-child(1) {
  padding: 0 10px;
  border-right: 1px solid gray;
}
.mini-card .mini-description {
  font-size: 12px;
  text-align: left;
  line-height: 20px;
  padding: 10px;
}



