
.slider-items {
  width: 800px;
  height: 600px;
  position: relative;
  border: 1px solid red;
}
.slider-items .slider-img {
  white-space: nowrap;
  width: 100%;
}
.slider-items .slider-img a {
  display: block;
}
.slider-items .slider-img a img {
  width: 100%;
  height: 600px;
  position: absolute;
  top: 0;
}
.slider-items .slider-num {
  position: absolute;
  bottom: 10px;
  right: 20px;
}
.slider-items .slider-num span {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  background-color: white;
  margin-left: 15px;
}
.slider-items .arrowspan {
  height: 100px;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 50%;
  color: white;
}
.slider-items .arrowspan span:nth-child(1) {
  position: absolute;
  left: 0;
  display: inline-block;
  line-height: 100px;
  font-size: 80px;
}
.slider-items .arrowspan span:nth-child(2) {
  position: absolute;
  right: 0;
  display: inline-block;
  line-height: 100px;
  font-size: 80px;
}
.spanstyle {
  font-size: 20px;
  color: red;
}
.slide-enter-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.slide-enter {
  -webkit-transform: translateX(800px);
          transform: translateX(800px);
}
.slide-next-leave-active {
  -webkit-transform: translateX(-800px);
          transform: translateX(-800px);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}



