
.checklist {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #038585;
}
.checklist .bill-header {
  margin: 0 auto;
  height: 60px;
  width: 1080px;
  line-height: 60px;
}
.checklist .bill-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1080px;
  margin: 0 auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.checklist .bill-left {
  padding: 40px;
  background-color: #e0e0e0;
  float: left;
}
.checklist .bill-right {
  margin-left: 20px;
  padding: 25px 20px 150px 30px;
  width: 600px;
  border: 1px solid #e0e0e0;
}
.checklist .bill-right .bill-address h4 {
  font-size: 16px;
  font-weight: bold;
  text-align: left;
}
.checklist .bill-right .bill-address div:nth-child(2) {
  width: 80%;
  line-height: 72px;
  background: #f1f1f1;
  text-align: center;
  color: #666;
  margin: 30px auto;
}
.checklist .bill-right .bill-check {
  text-align: left;
}
.checklist .bill-right .bill-check h4 {
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  margin-bottom: 30px;
}
.checklist .bill-right .bill-check span {
  margin-left: 20px;
  font-size: 12px;
  color: black;
}
.checklist .bill-right .bill-check div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.checklist .bill-right .bill-check div span {
  width: 45%;
  height: 70px;
  background: #f1f1f1;
  text-align: center;
  color: #666;
  line-height: 70px;
}
.checklist .bill-right .bill-privilege {
  margin: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.checklist .bill-right .bill-privilege h4 {
  line-height: 40px;
}
.checklist .bill-right .bill-privilege div {
  margin-left: 30px;
}
.checklist .bill-right .bill-privilege div select {
  width: 100px;
  height: 30px;
}
table {
  border-collapse: collapse;
  width: 300px;
  border: 2px solid pink;
}
table td {
  height: 30px;
}
.bill-submit {
  width: 100px;
  height: 40px;
  display: inline-block;
  padding: 8px 17px;
  line-height: 1;
  border: none;
  border-radius: 2px;
  outline: 0;
  cursor: pointer;
  font-size: 14px;
  text-align: center;
  background-color: #f74342;
  color: white;
  font-size: 16px;
}



