
.food-card-detail {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: pink;
  top: 0;
  left: 0;
  margin: auto;
  z-index: 50;
}
.food-card-detail .card-detail {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 900px;
  height: 460px;
  background-color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 30px 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.food-card-detail .card-detail .close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 20px;
}
.food-card-detail .card-detail .dialog-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 360px;
          flex: 0 0 360px;
  width: 360px;
  margin-right: 30px;
  height: 800px;
}
.food-card-detail .card-detail .dialog-left img {
  display: inline-block;
  width: 360px;
  height: 360px;
}
.food-card-detail .card-detail .dialog-right {
  width: 430px;
  text-align: left;
}
.food-card-detail .card-detail .dialog-right .dialog-name {
  font-size: 22px;
  color: #333;
  margin-bottom: .5em;
}
.food-card-detail .card-detail .dialog-right .dialog-description {
  border-top: 1px dashed red;
  border-bottom: 1px dashed red;
  font-size: 13px;
  line-height: 1.8em;
  padding: 5px;
}
.food-card-detail .card-detail .dialog-right .dialog-buy {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 43px;
  font-size: 28px;
  color: #f17530;
  padding: 20px 0;
}
.food-card-detail .card-detail .dialog-right .dialog-rating {
  width: 100%;
}
.food-card-detail .card-detail .dialog-right .dialog-rating .rating-header {
  font-size: 14px;
  color: #aaa;
  border-bottom: 1px solid #ddd;
  padding-bottom: 0.3em;
}
.food-card-detail .card-detail .dialog-right .dialog-rating .rating-item {
  height: 20px;
  border-bottom: 1px solid #ddd;
  padding: 18px 0;
  color: #999;
}
.food-card-detail .card-detail .dialog-right .dialog-rating .rating-item .ratingstar {
  display: inline-block;
}
.food-card-detail .card-detail .dialog-right .dialog-rating .rating-item > span {
  margin-left: 20px;
}
.food-card-detail .card-detail .dialog-right .dialog-rating .rating-item .ratingtext {
  line-height: 26px;
}
.cover {
  height: 200px;
  overflow: hidden;
}



