
.cart-empty {
  /*background-color: #0089dc;*/
  background-color: #0089dc;
  width: 90px;
  height: 26px;
  text-align: center;
  border-radius: 13px;
  font-size: 12px;
  line-height: 26px;
  color: white;
  cursor: pointer;
  z-index: 133;
}
.cart-full {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cart-full .cart-num {
  display: inline-block;
  width: 36px;
  height: 22px;
  text-align: center;
  vertical-align: top;
  border: none;
  text-decoration: none;
  font-size: 12px;
}
.cart-full .cart-min,
.cart-full .cart-plus {
  display: inline-block;
  width: 27px;
  height: 22px;
  line-height: 24px;
  background-color: white;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
}
.cart-full .cart-min {
  border-top-left-radius: 11px;
  border-bottom-left-radius: 11px;
}
.cart-full .cart-plus {
  border-top-right-radius: 11px;
  border-bottom-right-radius: 11px;
}



